import React from 'react';
import styled from 'styled-components';
import { useLanguage } from '../contexts/LanguageContext';

const ErrorContainer = styled.div`
  background: rgba(231, 76, 60, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(231, 76, 60, 0.3);
  border-radius: 16px;
  padding: 20px;
  color: #e74c3c;
  text-align: center;
  margin: 20px 0;
  box-shadow: 0 8px 32px 0 rgba(231, 76, 60, 0.2);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;

  &::before {
    content: '';
    font-size: 20px;
  }
`;

const translations = {
  zh: {
    error: '',
    defaultError: '',
    networkError: '',
    contractError: '',
    walletError: '',
    insufficientBalance: '',
    invalidAmount: '',
    invalidAddress: '',
    userRejected: '',
    tryAgain: ''
  },
  en: {
    error: 'Error',
    defaultError: 'An unknown error occurred. Please try again later',
    networkError: 'Network connection error',
    contractError: 'Contract call error',
    walletError: 'Wallet connection error',
    insufficientBalance: 'Insufficient balance',
    invalidAmount: 'Invalid amount',
    invalidAddress: 'Invalid address',
    userRejected: 'User rejected operation',
    tryAgain: 'Please try again'
  },
  ja: {
    error: '',
    defaultError: '',
    networkError: '',
    contractError: '',
    walletError: '',
    insufficientBalance: '',
    invalidAmount: '',
    invalidAddress: '',
    userRejected: '',
    tryAgain: ''
  },
  ko: {
    error: '',
    defaultError: '    .   ',
    networkError: '  ',
    contractError: '  ',
    walletError: '  ',
    insufficientBalance: ' ',
    invalidAmount: '  ',
    invalidAddress: '  ',
    userRejected: '  ',
    tryAgain: ' '
  }
};

interface ErrorMessageProps {
  message?: string;
  type?: keyof typeof translations.zh;
}

export const ErrorMessage: React.FC<ErrorMessageProps> = ({ 
  message,
  type = 'defaultError'
}) => {
  const { language } = useLanguage();
  const t = translations[language];

  const errorMessage = message || t[type];

  return (
    <ErrorContainer>
      {errorMessage}
    </ErrorContainer>
  );
}; 