import { useState, useCallback } from 'react';
import { useWeb3 } from '../contexts/Web3Context';
import { getMutualBankContract } from '../utils/contract';
import { ethers } from 'ethers';
import { TOKEN_ADDRESS } from '../config/contracts';
import { useContract } from './useContract';
import { MUTUAL_BANK_ABI } from '../contracts/abi';
import { useNotification } from '../contexts/NotificationContext';

// ERC20  ABI
const ERC20_ABI = [
  'function name() view returns (string)',
  'function symbol() view returns (string)',
  'function decimals() view returns (uint8)',
  'function totalSupply() view returns (uint256)',
  'function balanceOf(address) view returns (uint256)',
  'function transfer(address to, uint256 amount) returns (bool)',
  'function allowance(address owner, address spender) view returns (uint256)',
  'function approve(address spender, uint256 amount) returns (bool)',
  'function transferFrom(address from, address to, uint256 amount) returns (bool)',
  'event Transfer(address indexed from, address indexed to, uint256 amount)',
  'event Approval(address indexed owner, address indexed spender, uint256 amount)'
];

interface StakeCallbacks {
  onApproving?: () => void;
  onStaking?: () => void;
}

export const useStaking = () => {
  const { signer } = useWeb3();
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const { getContract } = useContract(MUTUAL_BANK_ABI);
  const { showNotification } = useNotification();

  const clearError = useCallback(() => {
    setError(null);
  }, []);

  const approve = useCallback(async (amount: string) => {
    if (!signer) {
      setError('');
      return false;
    }

    try {
      setLoading(true);
      setError(null);
      const contract = getMutualBankContract(signer);
      const tokenContract = new ethers.Contract(
        TOKEN_ADDRESS,
        ERC20_ABI,
        signer
      );

      const amountWei = ethers.utils.parseEther(amount);
      const tx = await tokenContract.approve(contract.address, amountWei);
      await tx.wait();
      return true;
    } catch (err: any) {
      console.error(':', err);
      if (err.code === 4001) {
        // 
        setError('');
      } else {
        setError(err.message || '');
      }
      return false;
    } finally {
      setLoading(false);
    }
  }, [signer]);

  const stake = useCallback(async (amount: string, referrer: string, callbacks?: {
    onApproving?: () => void;
    onStaking?: () => void;
  }): Promise<boolean> => {
    try {
      const contract = getContract();
      if (!contract) {
        throw new Error('');
      }

      return true;
    } catch (error: any) {
      console.error(':', error);
      return false;
    }
  }, [getContract]);

  const withdraw = useCallback(async (stakeIndex: number): Promise<boolean> => {
    try {
      const contract = getContract();
      if (!contract) {
        throw new Error('');
      }

      // 
      let retries = 3;
      let tx;
      
      while (retries > 0) {
        try {
          // 
          const stakeInfo = await contract.getUserStakeInfo(await contract.signer.getAddress(), stakeIndex);
          console.log(':', stakeInfo);
          
          if (!stakeInfo.active) {
            throw new Error('');
          }

          // 
          tx = await contract.withdraw(stakeIndex);
          console.log(':', tx.hash);
          break;
        } catch (err: any) {
          console.error(':', err);
          retries--;
          if (retries === 0) throw err;
          await new Promise(resolve => setTimeout(resolve, 1000));
        }
      }

      if (!tx) {
        throw new Error('');
      }

      // 
      const receipt = await tx.wait();
      console.log(':', receipt);

      return true;
    } catch (error: any) {
      console.error(':', error);
      if (error.message.includes('Stake not active')) {
        showNotification('error', '');
      } else {
        showNotification('error', error.message || '');
      }
      return false;
    }
  }, [getContract, showNotification]);

  const claimRewards = useCallback(async (): Promise<boolean> => {
    try {
      const contract = getContract();
      if (!contract) {
        throw new Error('');
      }

      // 
      let retries = 3;
      let tx;
      
      while (retries > 0) {
        try {
          tx = await contract.claimRewards();
          console.log(':', tx.hash);
          break;
        } catch (err) {
          retries--;
          if (retries === 0) throw err;
          await new Promise(resolve => setTimeout(resolve, 1000));
        }
      }

      if (!tx) {
        throw new Error('');
      }

      await tx.wait();
      return true;
    } catch (error: any) {
      console.error(':', error);
      return false;
    }
  }, [getContract]);

  return {
    stake,
    withdraw,
    claimRewards,
    loading,
    error,
    clearError
  };
};