import { useCallback, useState } from 'react';
import { ethers } from 'ethers';
import { useWeb3 } from '../contexts/Web3Context';
import { TOKEN_ADDRESS } from '../config/contracts';
import { useContract } from './useContract';
import { formatTokenAmount } from '../utils/format';

const TOKEN_ABI = [
  "function balanceOf(address owner) view returns (uint256)",
  "function decimals() view returns (uint8)",
  "function symbol() view returns (string)",
  "function transfer(address to, uint amount) returns (bool)",
  "function approve(address spender, uint256 amount) returns (bool)",
  "function allowance(address owner, address spender) view returns (uint256)"
];

export const useToken = () => {
  const { provider, account } = useWeb3();
  const { getContract } = useContract(TOKEN_ABI);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const getBalance = useCallback(async (address: string): Promise<string> => {
    if (!address) {
      console.error('');
      return '0';
    }

    setLoading(true);
    setError(null);

    try {
      const contract = getContract();
      if (!contract) {
        throw new Error('');
      }

      // 
      let retries = 3;
      let balance;
      
      while (retries > 0) {
        try {
          balance = await contract.balanceOf(address);
          break;
        } catch (err) {
          retries--;
          if (retries === 0) throw err;
          await new Promise(resolve => setTimeout(resolve, 1000)); // 1
        }
      }

      if (!balance) {
        throw new Error('');
      }

      return formatTokenAmount(balance.toString());
    } catch (err: any) {
      console.error(':', err);
      setError(err.message || '');
      return '0';
    } finally {
      setLoading(false);
    }
  }, [getContract]);

  const approve = useCallback(async (spender: string, amount: string): Promise<boolean> => {
    if (!spender || !amount) {
      console.error('');
      return false;
    }

    setLoading(true);
    setError(null);

    try {
      const contract = getContract();
      if (!contract) {
        throw new Error('');
      }

      // 
      const currentAllowance = await contract.allowance(await contract.signer.getAddress(), spender);
      if (currentAllowance.gte(ethers.utils.parseEther(amount))) {
        return true; // 
      }

      // 
      let retries = 3;
      let tx;
      
      while (retries > 0) {
        try {
          tx = await contract.approve(spender, ethers.utils.parseEther(amount));
          break;
        } catch (err) {
          retries--;
          if (retries === 0) throw err;
          await new Promise(resolve => setTimeout(resolve, 1000));
        }
      }

      if (!tx) {
        throw new Error('');
      }

      await tx.wait();
      return true;
    } catch (err: any) {
      console.error(':', err);
      setError(err.message || '');
      return false;
    } finally {
      setLoading(false);
    }
  }, [getContract]);

  const getAllowance = async (owner: string, spender: string): Promise<string> => {
    if (!provider || !owner || !spender) {
      console.error('Providerowner  spender ');
      return '0';
    }

    if (!TOKEN_ADDRESS) {
      console.error('');
      return '0';
    }

    try {
      console.log('...', {
        tokenAddress: TOKEN_ADDRESS,
        owner,
        spender,
        network: await provider.getNetwork()
      });

      // 
      const code = await provider.getCode(TOKEN_ADDRESS);
      console.log(':', code);
      
      if (code === '0x') {
        console.error('');
        return '0';
      }

      const tokenContract = new ethers.Contract(TOKEN_ADDRESS, TOKEN_ABI, provider);
      console.log(':', {
        address: tokenContract.address,
        interface: tokenContract.interface.format()
      });
      
      try {
        console.log(' allowance...');
        //  callStatic 
        const allowance = await tokenContract.callStatic.allowance(owner, spender);
        console.log(':', allowance.toString());
        const formattedAllowance = ethers.utils.formatEther(allowance);
        console.log(':', formattedAllowance);
        return formattedAllowance;
      } catch (err: any) {
        console.error('allowance :', err);
        if (err.code === 'CALL_EXCEPTION') {
          console.error(' allowance ');
          console.error(':', {
            code: err.code,
            message: err.message,
            data: err.data,
            transaction: err.transaction
          });
        }
        return '0';
      }
    } catch (error) {
      console.error(':', error);
      return '0';
    }
  };

  return {
    getBalance,
    approve,
    getAllowance,
    loading,
    error
  };
}; 